
import {setTitle} from '@/common/js/common';
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
;
import uploadfield from '../../components/uploadfield/uploadfield.vue';
import {mapState } from 'vuex'

const list = () => ['', '',''];

export default{
  data(){
    return{
      loading:true,
      index:0,
      list2: list(),
      click_tab:"",
      data1:[],
      acreage:"",
      house_type:"",
      max_price:"",
      min_price:"",
      content:"",
      is_checked:0,
      select_work:"",
      worktypes:[{"val":"0","desc":"","isselect":true},{"val":"1","desc":"","isselect":false},{"val":"2","desc":"","isselect":false}],
      confirm_show:false,
      dialog_title:"",
      confirm_content:"",
      isLoading:false,
      toast_text:"",
      show_toast:false
    }
  },
  computed: {
    ...mapState({
      picurls: state => state.picurls,
    })
  },
  components:{
    Loading,Tab,TabItem,Cell,Group,XButton,XInput,uploadfield,XTextarea,Checker,CheckerItem,Alert,Toast
  },
  mounted(){
    setTitle("");
    for(let i=0;i<this.worktypes.length;i++){
      if(this.worktypes[i].isselect){
        this.is_checked=i+1;
      }
    }
    this.getList();
  },
  methods:{
    resetPic(){
      this.$store.dispatch("resetContext");
    },
    go_back(){
      this.$router.go(-1);
    },
    onItemClick(index){
        this.index = index;
    },
    getList(){
        let self = this;
        if(service_url){
          this.$http.get(service_url+"/o2o/asset/wx/houseinfo?houseid="+userProfile.houseid).then((data)=>{
            self.data1=data.body.data;
          })
        }else{
          this.$http.get("/api/houseinfo").then((data)=>{
            self.data1=data.body.data.data;
          })
        }
      document.getElementById("index_loading").style.display="none";
    },
    validate(key){
      if(key===1){
        if(!this.acreage){
          this.confirm_content="";
          this.confirm_show=true;
          return false;
        }
        if(!this.house_type){
          this.confirm_content="";
          this.confirm_show=true;
          return false;
        }
        if(!this.min_price){
          this.confirm_content="";
          this.confirm_show=true;
          return false;
        }
        if(!this.max_price){
          this.confirm_content="";
          this.confirm_show=true;
          return false;
        }
        if(!this.picurls.length){
          this.confirm_content="";
          this.confirm_show=true;
          return false;
        }
        return true;
      }else{
        if(!this.acreage){
          this.confirm_content="";
          this.confirm_show=true;
          return false;
        }
        if(!this.picurls.length){
          this.confirm_content="";
          this.confirm_show=true;
          return false;
        }
        if(!this.select_work){
          this.confirm_content="";
          this.confirm_show=true;
          return false;
        }
        return true;
      }
    },
    submit1(){
      if(!this.validate(1)){
          return;
      }
      this.isLoading=true;
      let self = this;
      const items={
        "data":{
          "address":this.data1.position,
          "phone":this.data1.phone,
          "area":this.acreage, //
          "housetype":this.house_type,  // 
          "pricelow":this.min_price,//  
          "pricehigh":this.max_price,//  
          "remark":this.content,//
          "picurls":this.picurls
        }
      };
      if(service_url){
        this.$http.post(service_url+"/o2o/asset/wx/rent?houseid="+userProfile.houseid,items).then((data)=>{
          this.isLoading=false;
          if(data.body.statusCode==="ok"){
            this.show_toast=true;
            this.toast_text=data.body.message;
            setTimeout(self.go_back,1000);
            setTimeout(self.resetPic,1000);
          }else{
            this.confirm_content=data.body.message;
            this.confirm_show=true;
          }
        })
      }else{
        this.isLoading=false;
        this.show_toast=true;
        this.toast_text="";
        setTimeout(self.go_back,1000);
        setTimeout(self.resetPic,1000);
      }
    },
    submit2(){
      if(!this.validate(1)){
        return;
      }
      this.isLoading=false;
      const items={
        "data":{
          "address":this.data1.position,
          "phone":this.data1.phone,
          "area":this.acreage, //
          "housetype":this.house_type,  // 
          "pricelow":this.min_price,//  
          "pricehigh":this.max_price,//  
          "remark":this.content,//
          "picurls":this.picurls
        }
      };
      this.$http.post(service_url+"/o2o/asset/wx/sail?houseid="+userProfile.houseid,items).then((data)=>{
        this.isLoading=true;
        if(data.body.statusCode==="ok"){
          this.show_toast=true;
          this.toast_text=data.body.message;
          setTimeout(this.go_back,1000);
          setTimeout(this.resetPic,1000);
        }else{
          this.confirm_content=data.body.message;
          this.confirm_show=true;
        }
      })
    },
    submit3(){
      if(!this.validate(2)){
        return;
      }
      const items={
        "data":{
          "address":this.data1.position,
          "phone":this.data1.phone,
          "type":this.select_work, //
          "area":this.acreage, //
          "remark":this.content,//
          "picurls":this.picurls
        }
      };
      this.$http.post(service_url+"/o2o/asset/wx/trust?houseid="+userProfile.houseid,items).then((data)=>{
        if(data.body.statusCode==="ok"){
          this.show_toast=true;
          this.toast_text=data.body.message;
          setTimeout(this.go_back,1000);
          setTimeout(this.resetPic,1000);
        }else{
          this.confirm_content=data.body.message;
          this.confirm_show=true;
          setTimeout(this.resetPic,500);
        }
      })
    },
    itemclick(val){
      for(let i = 0;i<this.worktypes.length;i++){
        if(val===this.worktypes[i].desc){
          this.select_work=this.worktypes[i].val;
        }
      }
    },
  }
}



